<template>
  <div id="listBox" @dragenter="onOver" @dragover="onOver" @drop="onOver">
    <div class="conBox">
      <h4 class="blue">已经存在的用户</h4>
      <table v-if="existsClient.length > 0">
        <tr class="title">
          <td>ID</td>
          <td>用户名</td>
          <td>登录名</td>
          <td>状态</td>
        </tr>
        <tr v-for="item in existsClient">
          <td>{{item.clientID}}</td>
          <td>{{item.userName}}</td>
          <td>{{item.loginName}}</td>
          <td :class="{'green':item.status===0,'blue':item.status===1}">{{statusFilter(item.status)}}</td>
        </tr>
      </table>
      <h4 class="red">注册失败的用户</h4>
      <table v-if="failClients.length > 0">
        <tr class="title">
          <td>ID</td>
          <td>用户名</td>
          <td>登录名</td>
          <td>状态</td>
        </tr>
        <tr v-for="item in failClients">
          <td>{{item.clientID}}</td>
          <td>{{item.userName}}</td>
          <td>{{item.loginName}}</td>
          <td :class="{'green':item.status===0,'blue':item.status===1}">{{statusFilter(item.status)}}</td>
        </tr>
      </table>
      <h4 class="green">注册成功的用户</h4>
      <table v-if="successClients.length > 0">
        <tr class="title">
          <td>ID</td>
          <td>用户名</td>
          <td>登录名</td>
          <td>状态</td>
        </tr>
        <tr v-for="item in successClients">
          <td>{{item.clientID}}</td>
          <td>{{item.userName}}</td>
          <td>{{item.loginName}}</td>
          <td :class="{'green':item.status===0,'blue':item.status===1}">{{statusFilter(item.status)}}</td>
        </tr>
      </table>
    </div>
    <button @click.stop="closeList">确定</button>
  </div>
</template>

<script>
  export default{
    data() {
      return {}
    },
    props: {
      existsClient: {
        type: Array
      },
      failClients: {
        type: Array
      },
      successClients: {
        type: Array
      }
    },
    methods: {
      closeList() {
        this.$emit(`closeList`)
      },
      onOver(e) {
        e.stopPropagation();
        e.preventDefault();
      },
      statusFilter(num) {
        if (num === 0) {
          return `正常使用`
        } else if (num === 1) {
          return `等待管理员激活`
        }
      }
    }
  }
</script>

<style rel="stylesheet/less" lang="less">
  #listBox {
    width: 100%;
    min-height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    background: rgba(0, 0, 0, 0.6);
    text-align: center;
    > .conBox {
      width: 100%;
      height: auto;
      color: #fff;
      padding: 20px 0;
      .title{
        color:#ccc;
      }
      .green{
        color:#468847;
      }
      .red{
        color:#b94a48;
      }
      .blue{
        color:#3a87ad;
      }
      li {
        padding: 5px 0;
      }
      table{
        width:100%;
        table-layout: fixed;
        tr{
          height:30px;
        }
      }
    }
    > button {
      width: 50px;
      height: 30px;
      background: #ddd;
      border-radius: 5px;
      text-align: center;
      line-height: 30px;
      border: 0;
      cursor: pointer;
    }
  }
</style>



