<template>
  <div id="popupBox">
    <button class="closeBtn" @click.stop="closePop">x</button>
    <div class="topBox">
      <span>提示</span>
    </div>
    <div class="conBox">
      {{message}}
    </div>
    <div class="bottomBox">
      <button @click.stop="closePop">确定</button>
    </div>
  </div>
</template>

<script>
  export default{
    data() {
      return {
      }
    },
    props: {
      message: {
        type: String
      }
    },
    methods: {
      closePop() {
        this.$emit(`closePop`)
      }
    }
  }
</script>

<style rel="stylesheet/less" lang="less">
  #popupBox {
    width: 400px;
    height: 200px;
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    z-index: 4;
    margin: auto;
    border: 1px solid #ddd;
    border-radius: 5px;
    background: #ddd;
    text-align: center;
    > .closeBtn {
      position: absolute;
      right: 0;
      top: 0;
      width: 50px;
      height: 49px;
      text-align: center;
      line-height: 49px;
      border: 0;
      font-size: 18px;
      cursor: pointer;
    }
    > .topBox {
      width: 100%;
      height: 50px;
      line-height: 50px;
      border-bottom: 1px solid #ccc;
      font-size: 18px;
      box-sizing: border-box;
    }
    > .conBox {
      width:100%;
      height:100px;
      box-sizing: border-box;
      padding: 20px 0;
    }
    > .bottomBox {
      width: 100%;
      height: 50px;
      border-top: 1px solid #ccc;
      box-sizing: border-box;
      > button {
        position: absolute;
        right: 10px;
        bottom: 5px;
        width: 60px;
        height: 35px;
        background: #ccc;
        border-radius: 5px;
        text-align: center;
        line-height: 35px;
        border: 0;
        font-size: 15px;
        color: #fff;
        cursor: pointer;
      }
    }
  }
</style>



