import Vue from 'vue'
import Router from 'vue-router'
import UploadExcel from '@/components/UploadExcel'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: `/`,
      name: `UploadExcel`,
      component: UploadExcel
    }
  ]
})



