<template>
  <div>
    <div dragable="true" class="DragUpload" @dragenter="onEnter" @dragover="onOver" @drop="onDrop">
      <div>
        <div class="fileInput">
          <span>上传文件</span>
          <input type="file" @change="uploadFile" ref="fileInput">
          <a href="http://devinner.91xiezhu.com/reslovetest.xls" target="_blank" class="file">下载文档</a>
        </div>
        <div class="explan">
          <img src="../../static/image/resloveimg.png" alt="" class="explanImage">
        </div>
      </div>
    </div>
    <div class="shade" @dragenter="onEnter" @dragover="onOver" @drop="onOver" v-if="shadeShow">
      <img src="../../static/image/loading.gif" alt="">
    </div>
    <PopupBox :message="message" @closePop="closePop" v-if="popupBox"/>
    <ListBox :existsClient="existsClient" :failClients="failClients" :successClients="successClients"
             @closeList="closeList" v-if="listBox"/>
  </div>
</template>

<script>
  import axios from 'axios'
  import PopupBox from '@/components/PopupBox.vue'
  import ListBox from '@/components/ListBox.vue'
  export default {
    data () {
      return {
        shadeShow: false,
        message: ``,
        existsClient: [],
        failClients: [],
        successClients: [],
        popupBox: false,
        listBox: false,
        fileSize: 2097152
      }
    },
    components: {
      PopupBox,
      ListBox
    },
    methods: {
      onEnter(e) {
        e.stopPropagation();
        e.preventDefault();
      },
      onOver(e) {
        e.stopPropagation();
        e.preventDefault();
      },
      onDrop(e) {
        e.stopPropagation();
        e.preventDefault();
        //取得拖进来的文件
        const data = e.dataTransfer;
        const files = data.files[0];
        //将其传给真正的处理文件的函数
        if (files) {
          if (files.size > this.fileSize) {
            this.message = `文件大小不得大于2MB！`
            this.popupBox = true
          } else if (!files.name.includes(`xls`)) {
            this.message = `文件格式不正确！`
            this.popupBox = true
          } else {
            this.upload(files)
          }
        }
      },
      closePop() {
        this.popupBox = false
      },
      closeList() {
        this.listBox = false
      },
      uploadFile() {
        const files = this.$refs.fileInput.files[0];
        if (files) {
          if (files.size > this.fileSize) {
            this.message = `文件大小不得大于2MB！`
            this.popupBox = true
          } else if (!files.name.includes(`xls`)) {
            this.message = `文件格式不正确！`
            this.popupBox = true
          } else {
            this.upload(files)
          }
        }
      },
      async upload(file) {
        this.shadeShow = true;
        const excel = new FormData()
        excel.append(`file`, file)
        const result = await axios.post(`/resolve/upload`, excel).then(res => JSON.parse(res.request.response)).then(res => {
          if (res.status === 0) {
            return res.resultObject
          } else {
            return false
          }
        }).catch(err => {
          console.error(err)
          return false
        })
        this.shadeShow = false
        if (result) {
          this.existsClient = result.existsClient
          this.failClients = result.failClients
          this.successClients = result.successClients
          this.listBox = true
        } else {
          this.message = `上传文件失败！`
          this.popupBox = true
        }
      }
    }
  }
</script>

<style rel="stylesheet/less" lang="less">
  .DragUpload {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    background: #eee;
    > div{
      position: absolute;
      left: 0;
      top: 0;
      bottom: 0;
      right: 0;
      margin: auto;
      width: 600px;
      height: 200px;
    }
    .fileInput {
      float:left;
      position:relative;
      width: 300px;
      height: 200px;
      border: 1px dashed #666;
      border-radius: 5px;
      text-align: center;
      line-height: 200px;
      font-size: 20px;
      color: #666;
      > input {
        width: 100%;
        height: 100%;
        position: absolute;
        left: 0;
        top: 0;
        opacity: 0;
        cursor: pointer;
      }
      a{
        font-size:16px;
        color:#333;
        width:50%;
        height:50px;
      }
      .file{
        position:absolute;
        right:25%;
        top:100%;
      }
    }
    .explan{
      float:left;
      width:300px;
      height:200px;
      img{
        height:100%;
      }
    }
  }

  .shade {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    background: #fff;
    z-index: 2;
    > img {
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      top: 0;
      margin: auto;
    }
  }
</style>



